@import '../../../../UI/style.scss';

.follow {
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: 40%;
  max-width: 600px;
  margin: 0 auto;
  border-radius: 10px;

  @media (max-width: 700px) {
    width: 70%;
  }
}

.follow__title {
  margin-bottom: 0;
  margin-top: 50px;
  font-weight: 400;
  display: flex;
  justify-content: center;
}